import { getApiUrlWithToken, Logger } from '@uniformdev/common';
import fetch from 'node-fetch';
import { UniformServerConfig } from '../UniformServerConfig';

export function attachPreviewMiddleware(server: any, uniformConfig: UniformServerConfig, logger: Logger) {
    server.get('/uniform/api/content/*', getPreviewMiddleware(uniformConfig, logger));
}

export function getPreviewMiddleware(uniformConfig: UniformServerConfig, logger: Logger) {
    return async function (req: any, res: any) {
        const url = getApiUrlWithToken(uniformConfig, decodeURI(req.path), uniformConfig.UNIFORM_API_TOKEN);

        logger.debug('Proxying request: ' + url);
        try {
            const response = await fetch(url);
            const value = await response.text();
            logger.debug("Remote server's response is received, sending it to client...");

            res.setHeader('Content-Type', response.headers.get('Content-Type') || 'text/json');
            res.send(value);
        } catch (error) {
            logger.error('Failed to proxy request: ' + url, error);
            res.status(500).send(error);
        }
    };
}
